
<style scoped>
.f_sider {
  /* display: inline-block; */
  /* width: 24%; */
  max-width: 240px;
  min-width: 240px;
  float: left;
  height: 100%;
  padding-bottom: 48px;
}
</style>
<template>
  <div class="f_sider">
    <Menu
      @on-open-change="handleOpen"
      @on-select="handleSelect"
      :active-name="'1-1'"
      :open-names="['1']"
      class="Sider_uls"
      style="width: 100%"
    >
      <Submenu name="1">
        <template slot="title">
          <Icon type="ios-people" />用户管理
        </template>
        <MenuItem name="1-1">用户列表</MenuItem>
        <MenuItem name="1-2">新增用户</MenuItem>
      </Submenu>
      <Submenu name="2">
        <template slot="title">
          <Icon type="md-build" />工具
        </template>
        <MenuItem name="2-1">mockTools</MenuItem>
      </Submenu>
      <Submenu name="3">
        <template slot="title">
          <Icon type="md-document" />文档
        </template>
          <MenuItem name="3-1">API</MenuItem>
      </Submenu>
      <MenuItem name>退出</MenuItem>
    </Menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isCollapsed: false
    };
  },
  methods: {
    handleOpen(v) {},
    handleSelect(v) {
      if (v) {
        this.$emit("chageSider", v);
      } else {
        this.Logout();
      }
    },
    Logout() {
      this.$http({
        url: "/api/Logout",
        method: "get"
      })
        .then(res => {
          console.log("logout", res.data);
          setTimeout(() => {
            this.$router.push("/login");
          }, 500);
        })
        .catch(err => {
          this.$Message.error("退出失败");
        });
    }
  }
};
</script>